    top: 37%;
    right: 0;
  }
  .qiu {
    position: absolute;
    left: 10%;
  }
  .cent {
    background: url(../../assets/img/info/bg.png) top center no-repeat,
      url(../../assets/img/3.png) bottom center no-repeat;
    background-size: 100%, 100%;
    padding: 40px 0 50px;
    position: relative;
    span {
      font-size: 12px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      color: #aba8a8;
    }
    h3 {
      font-size: 16px;
      font-family: PingFangSC-Medium, PingFang SC;
