      padding: 20px 15px;
      box-sizing: border-box;
      width: 92%;
      margin: 0 auto 60px;
      position: relative;
      z-index: 2;
    }

    .ming {
      padding: 0 20px;
      margin-bottom: 60px;
      position: relative;
      z-index: 2;
      .ming_bix {
        background: linear-gradient(#d6f1ff, #ffffff);
        box-shadow: 0 7px 14px #77acf7;
        border-radius: 30px;
        padding: 30px 15px 20px;
        box-sizing: border-box;
        margin-top: 20px;
        .line {
          border-bottom: 1px solid #ddd;
          margin-bottom: 20px;
        }
        .btn_2 {
          width: 90%;
          background: url(../../assets/img/info/btn.png) top center no-repeat;
          background-size: 100%;
          margin: 5px auto 0;
          display: inline-block;
          border: none;
          text-align: center;
          color: #fff;
          font-family: PingFangSC-Medium, PingFang SC;
          font-weight: 500;
          font-size: 18px;
          padding: 11px 0;
        }
        .btn_line {
          width: 90%;
          background: url(../../assets/img/info/line.png) top center no-repeat;
          background-size: 100%;
