<template>
  <div class="info">
    <HeadTop :lang="lang"  @changeAppLocale="changeAppLocale"/>

    <div class="news">
      <h1>{{ $t("message.f_52") }}</h1>
      <h4 class="time">2022.01.27</h4>
      <p>{{ $t("message.f_53") }}</p>
      <p>{{ $t("message.f_54") }}</p>
       <p>{{ $t("message.f_53") }}</p>
      <p>{{ $t("message.f_54") }}</p>
       <p>{{ $t("message.f_53") }}</p>
      <p>{{ $t("message.f_54") }}</p>
   </div>
    
  </div>
</template>

<script type="es6">
import HeadTop from "../../components/Header";
import FontCon from "../../components/Footer";
import { mapActions, mapState } from "vuex";

export default {
  data() {
    return {
      lang: "",
    };
  },
computed: {
    ...mapState("main", ["hmdInfo", "mxtContract",'account']),
  },
   methods: {
    ...mapActions("main", ["add_hmdInfo", "add_mxtCoinContract"]),
       changeLanguage(lang) {
      this.$i18n.locale = lang;
      this.lang = lang;
    },
     changeAppLocale(locale) {
      this.changeLanguage(locale);
    },
    getMiningInfoFun: function (num) {
			var _this = this;
			_this.tabsNum = num;
         	_this.mxtContract.methods
        .getMiningInfo(_this.account)
        .call()
        .then((result) => {
          console.log(result.ct[11 + num])
         _this.F5data.allBonus = result.ct[11 + num];
				_this.F5data.balanceBonus = result.ct[7 + num];
				_this.F5data.vipLength = result.ct[15 + num];
        });
		},
  },
    mounted() {
   this.changeLanguage(localStorage.getItem("lang") || "zhCHS");  
  },

  components: {
    HeadTop,
    FontCon,
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped  lang="less">
.info {
  background: url(../../assets/img/info/bg.png) top center no-repeat,
    url(../../assets/img/3.png) center bottom no-repeat;
  background-size: 100%, 100%;
  padding-top: 15%;
  min-height:95%;
 .news{
   padding: 30px 20px;
   h1{
     font-size: 24px;
font-family: PingFangSC-Medium, PingFang SC;
font-weight: 500;
color: #323335;
   }
   h4{
     font-size: 14px;
font-family: PingFangSC-Regular, PingFang SC;
font-weight: 400;
color: #999999;
margin: 20px 0;
   }
   p{
     font-size: 14px;
font-family: PingFangSC-Regular, PingFang SC;
font-weight: 400;
color: #666666;
margin-bottom: 10px;
line-height: 24px;
   }
 }
}
</style>



