<template>
  <div class="home">
    <HeadTop  :isHome="isHome" :lang="lang" @changeAppLocale="changeAppLocale"/>
    <!-- <HeadTop
      @changeAppLocale="changeAppLocale"
      :type="type"
      :isHome="isHome"
      v-bind:lang="lang"
    /> -->
    <div id="home">
      <div class="topBox">
        <h1 class="textCenter">{{ $t("message.f_1") }}</h1>
        <h3 class="textCenter">{{ $t("message.f_2") }}</h3>
        <div class="swipe">
          <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
                    <van-swipe-item><img :src="require('../../assets/img/banner_1_'+ lang +'.png')" alt="" width="100%"></van-swipe-item>
                     <van-swipe-item><img :src="require('../../assets/img/banner_2_'+ lang +'.png')" alt="" width="100%"></van-swipe-item>
                          <!-- <van-swipe-item><img src="../../assets/img/banner.png" alt="" width="100%"></van-swipe-item> -->
        </van-swipe>
        </div>
      </div>
      <div class="pledge">
        <h2>{{ $t("message.f_3") }}</h2>
        <div class="inputBox">
          <van-row type="flex" justify="space-between">
            <van-col span="10" class="topPledge">
                <!-- <span>{{ $t("message.f_4") }}</span> -->
                <div class="bgWhite flex">
                <div class="bg_logo"><img src="../../assets/img/usdt.png" alt="" width="17"> <span style="font-size:12px">USDT</span></div>
              </div>
            </van-col>
            <van-col span="14"><span class="rightTxt">{{ $t("message.f_5") }}{{coinBalance | price}}</span></van-col>
          </van-row>
          <div class="put">
             <van-row type="flex" justify="space-between">
              <van-col span="12" class="topPledge">
                  <input type="number" placeholder="0" v-model="stakeAmount" @input="getNbn($event,2)" onkeyup="this.value=this.value.replace(/[^0-9]/g,'')"/>
              </van-col>
              <van-col span="4" class="textRight"><button @click="max">MAX</button></van-col>
               <van-col span="8"><div class="bg_logo" style="margin-left:10px"><img src="../../assets/img/usdt.png" alt=""> <span>USDT</span></div></van-col>
            </van-row>
          </div>
        </div>
        <button class="btn" @click="lpApprove" v-if="!isApprove" >{{ $t("message.f_6") }} USDT</button>
          <div class="textCenter"><img src="../../assets/img/1.png" alt="" width="28"></div>
         <div class="inputBox">
          <van-row type="flex" justify="space-between" align="center">
            <van-col span="12" class="topPledge">
                <!-- <span>{{ $t("message.f_4_1") }}</span> -->
                <div class="bgWhite flex">
                  <!-- @click="openCionList()" grcc打开-->
                <div class="bg_logo" >
                  <img :src="require('../../assets/img/'+ cionType +'.png')" alt="" width="20"> <span>{{ cionType }}</span>  
                  <img v-if="!showCionList" src="../../assets/img/jiao.png" alt="" width="10" class="jiao">
                  <img v-else src="../../assets/img/jiao2.png" alt="" width="10" class="jiao">
                  </div>
                <!-- <div class="bg_logo"><img src="../../assets/img/grcc.png" alt="" width="20"> <span>GRCC</span>  <i></i></div> -->
                <ul v-if="showCionList">
                  <li @click="changeCion('five')"><img src="../../assets/img/five.png" alt="" width="20"> <span>FIVE</span></li>
                  <li @click="changeCion('grcc')"><img src="../../assets/img/grcc.png" alt="" width="20"> <span>GRCC</span></li>
                </ul>
              </div>
            </van-col>
            <van-col span="12" v-if="cionType == 'five'"><span class="rightTxt">{{ $t("message.f_5") }}{{hmdBalance | priceFive}}</span></van-col>
             <van-col span="12" v-else><span class="rightTxt">{{ $t("message.f_5") }}{{GBalance | figure}}</span></van-col>
          </van-row>
          <div class="put">
             <van-row type="flex" justify="space-between">
              <van-col span="16" class="topPledge">
                  <input type="text" placeholder="0" disabled="disabled" v-model="nbn"/>
              </van-col>
              <!-- <van-col span="4" style="text-align: right;"><button>MAX</button></van-col> -->
               <van-col span="8"><div class="bg_logo" style="margin-left:10px"><img :src="require('../../assets/img/'+ cionType +'.png')" alt="" width="20"> <span>{{ cionType }}</span></div></van-col>
               <!-- <van-col span="8"><div class="bg_logo" style="margin-left:10px"><img src="../../assets/img/grcc.png" alt=""> <span>GRCC</span></div></van-col> -->
            </van-row>
          </div>
        </div>
        <div v-if="cionType == 'five'">
          <button class="btn" @click="stake" v-if="isFiveApprove">{{ $t("message.f_7") }} </button>
          <button class="btn" @click="FiveApprove" v-else>{{ $t("message.f_6") }} FIVE</button>
        </div>
        <div v-else>
          <button class="btn" @click="stake" v-if="isApproveGrcc">{{ $t("message.f_7") }} </button>
          <button class="btn" @click="GrccApprove" v-else>{{ $t("message.f_6") }} GRCC</button>
        </div>
        
      </div>

      <div class="contract">
        <h1 class="textCenter title">{{ $t("message.f_8") }}<span>{{ $t("message.f_9") }}</span> </h1>
        <div class="textCenter img_box"><img src="../../assets/img/img_1.png" alt="" width="70%"></div>
        <div class="textCenter img_box"><img src="../../assets/img/img_2.png" alt="" width="70%"></div>
      </div>

    </div>
    <div class="imgAll">
      <h1 class="textCenter title">{{ $t("message.f_10") }}<span>{{ $t("message.f_11") }}</span> </h1>
      <van-row>
      <!-- <van-col span="12"><img src="../../assets/img/img_3.png" alt="" width="100%"></van-col> -->
      <van-col span="12"><img src="../../assets/img/img_7.png" alt="" width="100%"></van-col>
      <van-col span="12"><img src="../../assets/img/img_4.png" alt="" width="100%"></van-col>
      <van-col span="12"><img src="../../assets/img/img_5.png" alt="" width="100%"></van-col>
      <van-col span="12"><img src="../../assets/img/img_6.png" alt="" width="100%"></van-col>
      <van-col span="12"><img src="../../assets/img/img_8.png" alt="" width="100%"></van-col>
      <!-- <van-col span="12"><img src="../../assets/img/img_9.png" alt="" width="100%"></van-col> -->
      <van-col span="12"><img src="../../assets/img/img_10.png" alt="" width="100%"></van-col>
      <!-- <van-col span="12"><img src="../../assets/img/img_11.png" alt="" width="100%"></van-col> -->
      <van-col span="12"><img src="../../assets/img/img_12.png" alt="" width="100%"></van-col>
      <van-col span="12"><img src="../../assets/img/img_13.png" alt="" width="100%"></van-col>
      <!-- <van-col span="12"><img src="../../assets/img/img_14.png" alt="" width="100%"></van-col> -->
    </van-row>
    </div>
    <!-- <FontCon /> -->
  </div>
</template>

<script type="es6">
import HeadTop from "../../components/Header";
import FontCon from "../../components/Footer";
import { mapActions, mapState } from "vuex";
import Web3 from "web3";
export default {
  data() {
    return {
      lang: "en",
      currency: "",
      link: "--",
      coinBalance: 0,
      isApprove: false,
      isFiveApprove:false,
      stakeAmount:null,
      withdrawAmount: "",
      timer: null,
      timer2: null,
      show: false,
      isHome:true,
      nbn:null,
      isApproveGrcc:false,
      showCionList:false,
      cionType:'five',
    };
  },
  computed: {
    ...mapState("main", [
      // "priceHMD",
      // "priceCNTM",
      "hmdInfo",
      "account",
      "balance",
      "mxtCoinContract",
      "mxtContract",
      "hmdBalance",
      "FiveCoinContract",
      "GCoinContract",
      "GBalance",
    ]),
  },
  components: {
    HeadTop,
    FontCon,
  },
  methods: {
    ...mapActions("main", [
      // "add_price",
      "add_hmdInfo",
      "add_mxtCoinContract",
      "add_hmdCoinContract",
      "add_gCoinContract"
    ]),
    openCionList(){
      this.showCionList=!this.showCionList;
    },
    changeCion(type){
      this.cionType = type;
       this.showCionList=false;
       if(type=='five'){
          var  Price2 = this.hmdInfo.fivePrice;
       }else{
          var Price2 = this.hmdInfo.GrccPrice;
          this.GrccisApproveFun();
       }
        var prex =4;
        var price = Number(Price2)*Number(this.stakeAmount);
        if(this.stakeAmount){
           // info.fivePrice =null;
          if (parseInt(price) == price){
             this.nbn = price;
            return 
          } 
          let len = 1;
          let i = 0;
          while (parseInt(price) != price) {
            price = price * 10;
              len *= 10;
              i++;
          }
          let last = Math.abs(i <= prex ? 0 : price % (Math.pow(10, (i - prex))));
          let d1 = price - last;
          if (last > 0) {
              d1 = d1 + Math.pow(10, (i - prex));
          }
          d1 = d1 / len;
          this.nbn = d1;
        }else{
          this.nbn = null;
        }
         
    },
    showPop: function (arg, callback) {
      if (arg == "show") {
        this.show = true;
      }
    },
    pirceInput(type) {
      if (type == 1) {
        let str = "" + this.stakeAmount;
        if (str.indexOf(".") !== -1) {
          let arr = str.split("");
          arr.splice(arr.length - 1);
          let str2 = arr.join("");
          this.stakeAmount = +str2;
        }
      } else {
        let str = "" + this.withdrawAmount;
        if (str.indexOf(".") !== -1) {
          let arr = str.split("");
          arr.splice(arr.length - 1);
          let str2 = arr.join("");
          this.withdrawAmount = +str2;
        }
      }
    },
    protectAddress: function (addr, num) {
      if (addr) {
        var newAddr = addr.replace('"', "").replace('"', "");
        if (newAddr == "0x0000000000000000000000000000000000000000") {
          return "--";
        }
        if (!newAddr) {
          return "--";
        }
        if (newAddr == 1) {
          return newAddr;
        }
        return (newAddr + "").length == 42
          ? newAddr.substring(0, num) + "****" + newAddr.substring(39, 42)
          : newAddr;
      }
    },
    changeLanguage(lang) {
      this.$i18n.locale = lang;
      this.lang = lang;
    },
    // 复制成功时的回调函数
    onCopy(e) {
      var txt = this.$t("message.copySuccee");
      this.$toast.success(txt);
    },
    // 复制失败时的回调函数
    onError(e) {
      var txt = this.$t("message.copyFail");
      this.$toast.fail(txt);
    },
    ceilNum (d, prex = 4){
        if (parseInt(d) == d) return d;
        let len = 1;
        let i = 0;
        while (parseInt(d) != d) {
            d = d * 10;
            len *= 10;
            i++;
        }
        let last = Math.abs(i <= prex ? 0 : d % (Math.pow(10, (i - prex))));
        let d1 = d - last;
        if (last > 0) {
            d1 = d1 + Math.pow(10, (i - prex));
        }
        d1 = d1 / len;
        return d1;
    },
    getNbn(e,type){
      if(type==1){
        var val = e;
      }else{
         var val = (e.target.value);
      }
      if(!e) return;
      if(this.cionType == 'five'){
        var Price2= this.hmdInfo.fivePrice;
      }else{
        var Price2= this.hmdInfo.GrccPrice;
      }
          var prex =4;
          var price = Number(Price2)*Number(val);
        
          // info.fivePrice =null;
          if (parseInt(price) == price){
             this.nbn = price;
            return 
          } 
          let len = 1;
          let i = 0;
          while (parseInt(price) != price) {
            price = price * 10;
              len *= 10;
              i++;
          }
          let last = Math.abs(i <= prex ? 0 : price % (Math.pow(10, (i - prex))));
          let d1 = price - last;
          if (last > 0) {
              d1 = d1 + Math.pow(10, (i - prex));
          }
          d1 = d1 / len;
           this.nbn = d1;
    },
    //取消抵押
    withdraw: function () {
      if (this.mxtContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
      var myzy = web3.utils.fromWei(_this.hmdInfo.info[19], "ether");
      if (myzy <= 0) {
        _this.$toast(_this.$t("message.noZy"));
        return;
      }
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.mxtContract.methods
        .exit()
        .send({ from: account })
        .then((res) => {
          console.log("withdraw", res);
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee"));
          _this.add_hmdInfo();
          _this.getCoinBalance();
          _this.add_hmdCoinContract();
          // }

          _this.withdrawAmount = "";
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    max: function () {
      var num = parseInt(this.coinBalance);
      var prec = 2;
      var ceil = true;
      // num = num.toString();
      // let index = num.indexOf(".");
      // if (index !== -1) {
      //   num = num.substring(0, 6 + index + 1);
      // } else {
      //   num = num.substring(0);
      // }
        const len = String(num).length;
  if (len <= prec) { return num }; 

  const mult = Math.pow(10, prec);
 this.stakeAmount =Math.floor(num / mult) * mult;
    

      // this.stakeAmount = num;
      this.getNbn(this.stakeAmount,1)
    },
    stake: function () {
      if (this.mxtContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var inviteCode = _this.hmdInfo.inviteCode.toString();
      var referrer = _this.hmdInfo.referrer.toString();
      var account = _this.account;

      if (!_this.isApprove) {
        _this.$toast(_this.$t("message.noAccredit"));
        return;
      }

      if (!referrer || referrer == "--") {
        referrer = _this.$route.query.invite ? _this.$route.query.invite.toString() : 1;
      }
      if (!inviteCode || inviteCode == "--") {
        inviteCode = _this.account;
      }

      if (inviteCode == referrer) {
        referrer = "1";
      }
if ((Number(_this.stakeAmount)  % 100) > 0 ) {
        _this.$toast(_this.$t("message.yu"));
        return;
      }
      if (Number(_this.stakeAmount) > Number(_this.coinBalance)) {
        _this.$toast(_this.$t("message.noBanner"));
        return;
      }
      

      if (Number(_this.balance / 10 ** 18) < 0) {
        _this.$toast(_this.$t("message.gas"));
        return;
      }
      if (Number(_this.stakeAmount) <= 0) {
        _this.$toast(_this.$t("message.noNum"));
        return;
      }
       if (Number(_this.stakeAmount) < 100) {
        _this.$toast(_this.$t("message.f_67"));
        return;
      }
      if (!referrer || referrer == "--") {
        _this.$toast(_this.$t("message.onReferees"));
        return;
      }
      var monye = (_this.stakeAmount * 10 ** 18).toString();
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      var tokenArr = _this.cionType =='five'?_this.ArrList.FiveArr:_this.ArrList.GtArr;
      console.log('investIn=======',inviteCode, referrer , web3.utils.toWei(_this.stakeAmount.toString(), 'ether'),tokenArr)
      this.mxtContract.methods
        .investIn(inviteCode, referrer , web3.utils.toWei(_this.stakeAmount.toString(), 'ether'),tokenArr)
        .send({
          from: account,
					})
        .then((res) => {
          console.log("investIn", res);
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee"));
          _this.add_hmdInfo();
          _this.getCoinBalance();
          _this.add_hmdCoinContract();
          //   setTimeout(function(){
          //      _this.add_hmdInfo();
          //     _this.getCoinBalance();
          //     _this.add_hmdCoinContract();
          //   },10000)
          // }

          _this.stakeAmount = '';
          _this.nbn="";
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    changeAppLocale(locale) {
      this.changeLanguage(locale);

      // this.$router.push({
      //   name: this.$route.name,
      //   params: { lang: this.lang },
      //   query: { r: localStorage.getItem("r") },
      // });
    },
       // GRCC授权
    GrccApprove: function () {
      if (this.GCoinContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var address = this.hmdInfo.address;
      var account = this.account;
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.GCoinContract.methods
        .approve(address, "10000000000000000000000000")
        .send({ from: account })
        .then((res) => {
          console.log("approve+GrccApprove", res);
          this.$toast.clear();
          this.$toast.success(this.$t("message.accredit"));
          this.isApproveGrcc = true;
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
           // Five授权
    FiveApprove: function () {
      if (this.mxtContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var address = this.hmdInfo.address;
      var account = this.account;
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.FiveCoinContract.methods
        .approve(address, "10000000000000000000000000")
        .send({ from: account })
        .then((res) => {
          console.log("approve", res);
          this.$toast.clear();
          this.$toast.success(this.$t("message.accredit"));
          this.isFiveApprove = true;
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
        // 是否授权
    FiveisApproveFun: function () {
      var account = this.account;
      var address = this.hmdInfo.address;

      this.FiveCoinContract.methods
        .allowance(account, address)
        .call()
        .then((res) => {
          var a = web3.utils.fromWei(res, "ether");
          if (a > 0) {
            this.isFiveApprove = true;
          }
        });
    },

    // USDT授权
    lpApprove: function () {
      if (this.mxtContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var address = this.hmdInfo.address;
      var account = this.account;
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.mxtCoinContract.methods
        .approve(address, "10000000000000000000000000")
        .send({ from: account })
        .then((res) => {
          console.log("approve", res);
          this.$toast.clear();
          this.$toast.success(this.$t("message.accredit"));
          this.isApprove = true;
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    getUrl: function () {
      var hash = window.location.hash;
      if (hash.indexOf("?") != -1) {
        hash = hash.split("?")[0];
      }
      var url = window.location.origin + window.location.pathname + hash;
      var newAddr = "";
      if (this.hmdInfo) {
        if (this.hmdInfo.referrer) {
          var addr = this.hmdInfo.inviteCode;
          newAddr = addr.replace('"', "").replace('"', "");
          this.link = url + "?r=" + newAddr;
        } else {
          this.link = url;
        }
      }
      return this.link;
    },
    // 是否授权
    isApproveFun: function () {
      var account = this.account;
      var address = this.hmdInfo.address;

      this.mxtCoinContract.methods
        .allowance(account, address)
        .call()
        .then((res) => {
          console.log('usdt=========isApproveFun',res)
          var a = web3.utils.fromWei(res, "ether");
          if (a > 0) {
            this.isApprove = true;
          }
        });
    },
       // GRCC是否授权
    GrccisApproveFun: function () {
      var account = this.account;
      var address = this.hmdInfo.address;

      this.GCoinContract.methods
        .allowance(account, address)
        .call()
        .then((res) => {
          console.log('=========GCoinContract',res)
          var a = web3.utils.fromWei(res, "ether");
          if (a > 0) {
            this.isApproveGrcc = true;
          }
        });
    },
    //获取当前币种余额
    getCoinBalance: function () {
      if (this.mxtCoinContract) {
        var account = this.account;
        this.mxtCoinContract.methods
          .balanceOf(account)
          .call()
          .then((res) => {
            var num = web3.utils.fromWei(res, "ether");
            num = num.toString();
            let index = num.indexOf(".");
            if (index !== -1) {
              num = num.substring(0, 6 + index + 1);
            } else {
              num = num.substring(0);
            }
            this.coinBalance = num;
          });
      }
    },
    get_param: function (name) {
      var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
      var r = window.location.search.substr(1).match(reg);
      if (r != null) {
        return unescape(r[2]);
      }
      // console.log(r)
      return null;
    },
  },
  created() {
      this.add_mxtCoinContract();
      this.getCoinBalance();
  },
  mounted() {
    var _this = this;
    this.changeLanguage(localStorage.getItem("lang") || "en");
    if (this.$route.query.invite) {
      localStorage.setItem("invite", this.$route.query.invite);
    }
    var getData = () => {
      this.add_hmdInfo();
      this.add_mxtCoinContract();
      this.add_hmdCoinContract();
      this.add_gCoinContract();
    };

    var getDataCalculate = () => {
      getData();
      this.timer = setInterval(() => {
        if (!this.hmdInfo || !this.mxtCoinContract || !this.hmdBalance) {
          getData();
        } else {
          this.getCoinBalance();
          this.isApproveFun();
          this.FiveisApproveFun();
          this.GrccisApproveFun();
          clearInterval(this.timer);
        }
      }, 1000);
    };
    getDataCalculate();
  
    // 每三十秒，刷新一次
    this.timer2 = setInterval(() => {
      if (this.timer) {
        clearInterval(this.timer);
        this.timer = null;
      }
      getDataCalculate();
    }, 80000);
  },
  destroyed() {
    if (this.timer) {
      clearInterval(this.timer);
      this.timer = null;
    }
    if (this.timer2) {
      clearInterval(this.timer2);
      this.timer2 = null;
    }
  },
};
</script>

