          </van-row> -->
        <!-- <button class="btn">{{ $t("message.f_51") }}</button> -->
      </div>

       <h1 class="textCenter title">
       {{ $t("message.f_63") }}<span>{{ $t("message.f_64") }}</span>
      </h1>

             <div class="my">
          
          <div class="list">
            <van-row type="flex" justify="space-between">
              <van-col span="12" class="textLeft"
                ><span>{{ $t("message.f_65") }}</span></van-col
              >
