             <van-row type="flex" justify="space-between" v-if="hmdInfo && hmdInfo.info[27]!=0 ">
              <van-col span="12" class="textLeft"
                ><span>{{$t("message.time")}}</span></van-col
              >
              <van-col span="12" class="textRight"
                ><span>
                  <van-count-down :time="hmdInfo.info[27] ? hmdInfo.info[27]*1000 : 0" />
                 </span></van-col
