</template>

<script type="es6">
import HeadTop from "../../components/Header";
import FontCon from "../../components/Footer";
import { mapActions, mapState } from "vuex";

export default {
  data() {
    return {
      lang: "",
      tabsNum: 1,
      F5data: {
        allBonus: 0,
        balanceBonus: 0,
        vipLength: 0,
      },
      //   currency: "",
      //   link: "--",
      //   coinBalance: 0,
      //   isApprove: false,
      //   stakeAmount: 1,
      //   withdrawAmount: 0,
      //   timer: null,
      //   timer2: null,
      //   show: false,
    };
  },
  computed: {
    ...mapState("main", ["hmdInfo", "mxtContract", "account"]),
  },
  methods: {
    ...mapActions("main", ["add_hmdInfo", "add_mxtCoinContract"]),
    changeLanguage(lang) {
      this.$i18n.locale = lang;
      this.lang = lang;
    },
    changeAppLocale(locale) {
      this.changeLanguage(locale);
    },
    getMiningInfoFun: function (num) {
      var _this = this;
      _this.tabsNum = num;
      _this.mxtContract.methods
        .getMiningInfo(_this.account)
        .call()
        .then((result) => {
          console.log(result.ct[11 + num]);
          _this.F5data.allBonus = result.ct[11 + num];
          _this.F5data.balanceBonus = result.ct[7 + num];
          _this.F5data.vipLength = result.ct[15 + num];
        });
    },
  },
  mounted() {
    var _this = this;
    this.changeLanguage(localStorage.getItem("lang") || "zhCHS");
    var getData = () => {
      this.add_hmdInfo();
      this.add_mxtCoinContract();
    };

    var getDataCalculate = () => {
      getData();
      this.timer = setInterval(() => {
        if (!this.hmdInfo || !this.mxtContract) {
          getData();
        } else {
          this.getMiningInfoFun(1);
          clearInterval(this.timer);
        }
      }, 1000);
    };

    getDataCalculate();
    // 每三十秒，刷新一次
    this.timer2 = setInterval(() => {
      if (this.timer) {
        clearInterval(this.timer);
        this.timer = null;
      }
      getDataCalculate();
    }, 60000);
  },
  destroyed() {
    if (this.timer) {
      clearInterval(this.timer);
      this.timer = null;
    }
    if (this.timer2) {
      clearInterval(this.timer2);
      this.timer2 = null;
    }
  },
  components: {
    HeadTop,
    FontCon,
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped  lang="less">
.info {
  .van-count-down{color: #75a9db;}
  background: url(../../assets/img/bg1.png) top center no-repeat,
    url(../../assets/img/info/bg2.png) center bottom no-repeat;
  background-size: 100%, 100%;
  padding-top: 15%;
  height: 95%;
  .title {
    font-size: 26px;
    font-family: SourceHanSansCN-Bold, SourceHanSansCN;
    font-weight: bold;
    color: #4da9db;
    line-height: 39px;
    margin: 0 0 15px;
    span {
      color: #ffa0ae !important;
      font-size: 26px !important;
      font-weight: bold !important;
    }
  }
  .line_box {
    width: 92%;
    margin: 20px auto;
    background: url(../../assets/img/line_box.png) top center no-repeat;
  background-size: 100%;
  position: relative;
  padding: 20px;
  box-sizing: border-box;
    span{
      display: inline-block;
      background: linear-gradient(to right,#FFA4EA,#3BA3D6);
      border-radius:0 16px 0 16px;
      font-size: 14px !important;
      font-family: PingFangSC-Semibold, PingFang SC;
      font-weight: 600;
      color: #FFFFFF !important;padding:10px 20px;
      position: absolute;
      right: 1px;
      top: 1px;
    }
    .bt {
      font-size: 14px;
      font-family: PingFangSC-Semibold, PingFang SC;
      font-weight: 600;
      color: #697896;
      margin-bottom:10px;
    }

    h2 {
      font-size: 24px;
      font-family: PingFangSC-Medium, PingFang SC;
      font-weight: 500;
      color: #334260;
    }
    h4 {
      font-size: 14px;
      font-family: PingFangSC-Medium, PingFang SC;
      font-weight: 500;
      color: #334260;
    }
  }
  // .cent::before{
  //     content: '';
  //     display: inline-block;
  //     position: absolute;
  //     width: 59px;
  //     height: 85px;
  //     background: url(../../assets/img/info/1.png) top center no-repeat;
  // background-size: 100%;
  // top: 3%;
  // left: 0;
  // }
  //     .cent::after{
  //     content: '';
  //     display: inline-block;
  //     position: absolute;
  //     width: 59px;
  //     height: 85px;
  //     background: url(../../assets/img/info/2.png) top center no-repeat;
  // background-size: 100%;
  // top: 37%;
  // right: 0;
  // }
  // .qiu{position: absolute;left: 10%;}
  .cent {
    // background: url(../../assets/img/info/bg.png) top center no-repeat,url(../../assets/img/3.png) bottom center no-repeat;
    // background-size: 100%,100%;
    padding: 20px 0 50px;
    position: relative;
    span {
      font-size: 12px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      color: #aba8a8;
    }
    h3 {
      font-size: 16px;
      font-family: PingFangSC-Medium, PingFang SC;
      font-weight: 500;
      color: #323335;
      margin-bottom: 15px;
      font-weight: bold;
      margin-top: 5px;
    }

    .ming {
      padding: 0 20px;
      margin-bottom: 40px;
      position: relative;
      z-index: 2;
      .ming_bix {
        background: linear-gradient(#d6f1ff, #ffffff);
        box-shadow: 0 7px 14px #77acf7;
        border-radius: 15px;
        overflow: hidden;
        box-sizing: border-box;
        margin-top: 20px;
        .tab {
          div {
            padding: 20px 0 15px;
            span {
              color: #999999;
              font-size: 15px;
            }
          }
          .in {
            background: #fff;
            border-radius: 15px 15px 0 0;
            span {
              color: #4da9db;
              font-weight: bold;
            }
          }
        }

      }
      p {
        font-size: 13px;
        font-family: PingFangSC-Regular, PingFang SC;
        font-weight: 400;
        line-height: 22px;
        color: #666666;
        text-align: center;
      }
      .ul_box {
        margin: 30px 0;
        span {
          display: inline-block;
          font-size: 15px;
          font-family: PingFangSC-Regular, PingFang SC;
          font-weight: 400;
          color: #666666;
          padding: 10px 0;
        }
        .textRight span {
          font-weight: 600;
          color: #323335;
        }
      }
      .btn {
        width: 100%;
        margin: 20px auto;
        display: block;
        border: none;
        background: linear-gradient(#3ed3e7, #6089f5);
        box-shadow: 0 3px 6px #a4baeb;
        border-radius: 15px;
        text-align: center;
        color: #fff;
        font-family: PingFangSC-Medium, PingFang SC;
        font-weight: 500;
        font-size: 17px;
        padding: 16px 0;
      }
    }
  }
}
 .my {
      background: linear-gradient(#fff3f5, #ffffff);
      box-shadow: 0 7px 14px #77acf7;
      border-radius: 30px;
      // padding: 30px 20px;
      box-sizing: border-box;
      width: 90%;
      margin: 0 auto 60px;
      position: relative;
      z-index: 2;
         .list {
           background: none;
         }
    }

        .list {
          background: #fff;
          border-radius: 0 0 15px 15px;
          padding: 20px;
          span {
            display: inline-block;
            padding: 6px 0;
          }
          .textRight span {
            font-size: 15px;
            font-family: PingFangSC-Medium, PingFang SC;
            font-weight: 600;
            color: #4da9db;
          }
        }
</style>



